import React from 'react';
import PropTypes from 'prop-types';
import {
  Box, Typography, Link,
} from '@mui/material';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';
import useHomePageStyle from '../styles/HomePageStyles.jsx';
import Avatar from '../assets/avatar.jpg';

const HomePage = ({ isMobile }) => {
  const {
    root,
    rootMobile,
    profileContainer,
    profileContainerMobile,
    avatar,
    name,
    nameMobile,
    description,
    descriptionMobile,
    links,
    link,
  } = useHomePageStyle();

  const homePageClass = isMobile ? `${rootMobile} ${root}` : root;
  const profileContainerClass = isMobile ? `${profileContainer} ${profileContainerMobile}` : profileContainer;

  return (
    <Box className={homePageClass}>
      <Box className={profileContainerClass}>
        <img className={avatar} src={Avatar} alt='' />
        <Typography className={isMobile ? `${name} ${nameMobile}` : name}>Tingjun Man</Typography>
        {!isMobile && <Typography className={description}>
          Software Engineer / Traveler / Amateur Bartender / Day Dreamer
        </Typography>}
        {isMobile && <Box>
          <Typography className={`${description} ${descriptionMobile}`}>
            Software Engineer
          </Typography>
          <Typography className={`${description} ${descriptionMobile}`}>
            Traveler
          </Typography>
          <Typography className={`${description} ${descriptionMobile}`}>
            Amateur Bartender
          </Typography>
          <Typography className={`${description} ${descriptionMobile}`}>
            Day Dreamer
          </Typography>
        </Box>}
        <Box className={links}>
          <Link className={link} href='https://www.linkedin.com/in/tingjun-man/' target='_blank' color='inherit' >
            <LinkedInIcon fontSize='large' />
          </Link>

          <Link className={link} href='https://github.com/tm2838' target='_blank' color='inherit'>
            <GitHubIcon fontSize='large' />
          </Link>

          <Link className={link} href='mailto:tingjun.man@gmail.com?subject=Hi Tingjun' color='inherit'>
            <EmailIcon fontSize='large' />
          </Link>
        </Box>
      </Box>
    </Box>
  );
};

HomePage.propTypes = {
  isMobile: PropTypes.bool,
};

export default HomePage;
